<template>
  <section class="main-page">
		<div class="left-menu">
			<img :src="menuImageIconFlag ? 'static/image/mainPage/menu-open.png' : 'static/image/mainPage/menu-unopen.png'" alt="" @click="menuImageIconFlag = !menuImageIconFlag">
			<!-- <div class="menu-icon"></div> -->
			<div :class="currentTopMenu ? 'menu-item' : 'menu-item another'" v-for="(item, index) in menuArray" :key="index" @click="leftMenuClick(item.label)">
				<div :class="activeMenu === item.label ? 'point active-point' : 'point'"></div>
				<div v-if="currentTopMenu" :class="activeMenu === item.label ? 'content active-content' : 'content'">{{item.label}}</div>
				<div v-if="!currentTopMenu" :class="activeMenu === item.label ? 'content-another active-content' : 'content-another'">{{item.label}}</div>
			</div>
		</div>
		<div class="top-menu">
			<div class="menu-item" v-for="(item, index) in topMenuArray" :key="index" @click="topMenuClick(item.label)">
				<div :class="currentTopMenu === item.label ? 'point show' : 'point unShow'"></div>
				<div :class="currentTopMenu === item.label ? 'content selected' : 'content'">{{item.label}}</div>
			</div>
		</div>
		<router-view/>
	</section>
</template>
<script>
export default {
	data () {
		return {
			menuImageIconFlag: true,
			currentTopMenu: 'Collection',
			menuArray: [
				{label: 'WeChart'},
				{label: 'E-mail'},
			],
			collectionMenuArray: [
				{label: 'WeChart'},
				{label: 'E-mail'},
			],
			activeMenu: '',
			workMenuArray: [
				{label: '华夏官网'},
				{label: '拳友官网'},
				{label: '哈云官网'},
				{label: '手机端'},
				{label: '手绘'},
			],
			topMenuArray: [
				{label: 'Collection'},
				{label: 'Introduction'},
			]
		}
	},
	mounted() {
		if (location.href.indexOf('works') > 0) {
			this.menuArray = this.workMenuArray;
			this.currentTopMenu = '';
			this.activeMenu = '华夏官网';
		} else if (location.href.indexOf('introduction') > 0) {
			this.currentTopMenu = 'Introduction';
		}
		this.$bus.on('imgCardClick', data => {
			this.currentTopMenu = '';
			this.activeMenu = data.name;
			this.menuArray = this.workMenuArray;
			this.$router.push({
				name: 'works',
				params: {
					label: data.name
				}
			});
		});
	},
	methods: {
		// 顶部菜单点击事件
		topMenuClick(label) {
			this.menuArray = this.collectionMenuArray;
			this.currentTopMenu = label;
			this.activeMenu = '';
			this.$router.push(label.toLocaleLowerCase());
		},
		// 左侧菜单点击事件
		leftMenuClick(label) {
			this.activeMenu = label;
			this.$bus.emit('worksMenuClick', label);
		}
	}
}
</script>
<style lang='less' scoped>
	.main-page {
		position: relative;
		display: flex;
		height: 100vh;
		background-color: #f5f5f5;
		.left-menu {
			position: absolute;
			top: 4%;
			left: 3%;
			z-index: 2;
			// .menu-icon {
			// 	width: 50px;
    	// 	height: 35px;
			// 	background-image: url('/static/image/mainPage/menu-open.png');
			// 	background-size: 80%;
			// 	background-repeat: no-repeat;
			// 	cursor: pointer;
			// }
			img {
				width: 3rem;
				cursor: pointer;
			}
			.menu-item {
				width: 1rem;
				height: 6rem;
				margin-top: 1rem;
				cursor: pointer;
				.point {
					position: relative;
					left: 0.15rem;
					width: .8rem;
					height: .8rem;
					background: rgba(89,89,89,1);
					border-radius: 50%;
				}
				.content {
					margin-top: .3rem;
					white-space: nowrap;
					font-size: 1.5rem;
					font-family: PingFangSC-Medium;
					font-weight: 500;
					color: rgba(140,140,140,1);
					line-height: 15px;
					transform: rotate(90deg);
				}
				.content-another {
					font-size: 1.5rem;
					left: -.1rem;
					position: relative;
					margin-top: .3rem;
				}
			}
			.another {
				height: auto;
			}
			.active-point {
				background:  #409EFF !important;
			}
			.active-content {
				color: #409EFF !important;
			}
		}
		.top-menu {
			z-index: 1;
			width: 100%;
			position: absolute;
			top: 5%;
			display: flex;
			justify-content: center;
			align-items: center;
			.menu-item {
				cursor: pointer;
				margin-left: 2rem;
				display: flex;
				justify-content: center;
				align-items: center;
				.point {
					width: .8rem;
					height: .8rem;
					border-radius:50%;
				}
				.content {
					margin-left: .5rem;
					font-size: 1.8rem;
					font-family: Arial-BoldMT;
					font-weight: bold;
					color:rgba(140,140,140,1);
				}
				.selected {
					color: rgba(38,38,38,1);
				}
				.show {
					background:rgba(38,38,38,1);
				}
				.unShow {
					background: transparent !important;
				}
			}
		}
	}
	.menu-item:hover {
		.point {
			background:  #409EFF !important;
		}
		.content {
			color: #409EFF !important;
		}
		.content-another {
			color: #409EFF !important;
		}
	}
</style>



